"use client";

import React, { useState } from 'react';

interface Location {
  id: number;
  name: string;
  category: string;
  description: string;
  priceRange: string;
  distance: string;
  coords: [number, number];
}

const sampleLocations: Location[] = [
  {
    id: 1,
    name: "Làng gốm Bát Tràng",
    category: "craft",
    description: "Làng nghề gốm sứ truyền thống với lịch sử hơn 700 năm",
    priceRange: "200,000 - 500,000 VND",
    distance: "15km từ Hà Nội",
    coords: [21.0285, 105.8542]
  },
  {
    id: 2,
    name: "Festival Đền Hùng",
    category: "festival",
    description: "Lễ hội truyền thống tôn vinh các vua Hùng",
    priceRange: "Free - 100,000 VND",
    distance: "85km từ Hà Nội",
    coords: [21.4167, 105.2333]
  },
  {
    id: 3,
    name: "Workshop Thêu Tay",
    category: "workshop",
    description: "Học nghệ thuật thêu tay truyền thống Việt Nam",
    priceRange: "300,000 - 800,000 VND",
    distance: "5km từ trung tâm",
    coords: [21.0245, 105.8412]
  }
];

export default function HeritageMapExplorer() {
  const [selectedCategory, setSelectedCategory] = useState('');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [selectedLocation, setSelectedLocation] = useState<Location | null>(null);

  const filteredLocations = sampleLocations.filter(location => {
    const matchesCategory = !selectedCategory || location.category === selectedCategory;
    const matchesKeyword = !searchKeyword || 
      location.name.toLowerCase().includes(searchKeyword.toLowerCase()) ||
      location.description.toLowerCase().includes(searchKeyword.toLowerCase());
    return matchesCategory && matchesKeyword;
  });

  return (
    <div className="heritage-map-explorer border border-gray-300 rounded p-6">
      <h2 className="text-2xl font-semibold mb-4">Heritage Map Explorer</h2>
      
      {/* Search and Filter Controls */}
      <div className="controls mb-6 space-y-4">
        <input
          type="text"
          placeholder="Tìm kiếm theo từ khóa..."
          value={searchKeyword}
          onChange={(e) => setSearchKeyword(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <select
          value={selectedCategory}
          onChange={(e) => setSelectedCategory(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        >
          <option value="">Tất cả danh mục</option>
          <option value="craft">Nghề thủ công</option>
          <option value="festival">Lễ hội</option>
          <option value="workshop">Workshop</option>
          <option value="homestay">Homestay</option>
          <option value="food">Ẩm thực</option>
        </select>
      </div>

      {/* Map Container */}
      <div className="map-container relative bg-gradient-to-br from-green-100 to-blue-100 rounded-lg h-96 mb-6 overflow-hidden">
        <div className="absolute inset-0 flex items-center justify-center text-gray-600 text-lg font-semibold">
          Bản đồ Việt Nam - Heritage Locations
        </div>
        
        {/* Location Blocks */}
        <div className="absolute inset-0 p-4">
          {filteredLocations.map((location, index) => (
            <div
              key={location.id}
              className={`absolute w-4 h-4 rounded-full cursor-pointer transform hover:scale-150 transition-transform ${
                location.category === 'craft' ? 'bg-red-500' :
                location.category === 'festival' ? 'bg-yellow-500' :
                location.category === 'workshop' ? 'bg-blue-500' : 'bg-green-500'
              }`}
              style={{
                left: `${20 + index * 15}%`,
                top: `${30 + index * 10}%`
              }}
              onClick={() => setSelectedLocation(location)}
            />
          ))}
        </div>
      </div>

      {/* Location Details Popup */}
      {selectedLocation && (
        <div className="location-popup border border-gray-300 rounded p-4 bg-white shadow-lg">
          <div className="flex justify-between items-start mb-2">
            <h3 className="text-xl font-semibold">{selectedLocation.name}</h3>
            <button
              onClick={() => setSelectedLocation(null)}
              className="text-gray-500 hover:text-gray-700"
            >
              ✕
            </button>
          </div>
          <p className="text-gray-600 mb-2">{selectedLocation.description}</p>
          <div className="details space-y-1 text-sm">
            <p><strong>Khoảng cách:</strong> {selectedLocation.distance}</p>
            <p><strong>Giá:</strong> {selectedLocation.priceRange}</p>
          </div>
          <button className="mt-4 bg-red-800 text-white px-4 py-2 rounded hover:bg-red-900 transition">
            Book ngay
          </button>
        </div>
      )}

      {/* Location List */}
      <div className="location-list">
        <h3 className="text-lg font-semibold mb-3">Danh sách địa điểm ({filteredLocations.length})</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {filteredLocations.map((location) => (
            <div
              key={location.id}
              className="location-card border border-gray-200 rounded p-3 cursor-pointer hover:shadow-md transition-shadow"
              onClick={() => setSelectedLocation(location)}
            >
              <h4 className="font-semibold">{location.name}</h4>
              <p className="text-sm text-gray-600 mb-2">{location.description}</p>
              <div className="text-xs text-gray-500">
                <p>{location.distance}</p>
                <p>{location.priceRange}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
