"use client";

import React, { useState } from 'react';

export default function Chatbot() {
  const [messages, setMessages] = useState<string[]>([]);
  const [input, setInput] = useState('');
  const [error, setError] = useState('');

  const sendMessage = async () => {
    if (!input.trim()) return;
    setError('');
    setMessages((prev) => [...prev, `User: ${input}`]);
    try {
      const res = await fetch('/api/chatbot', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ prompt: input }),
      });
      if (!res.ok) throw new Error('API error');
      const data = await res.json();
      setMessages((prev) => [...prev, `Bot: ${data.response}`]);
      setInput('');
    } catch (err) {
      setError('Có lỗi xảy ra! Vui lòng thử lại.');
    }
  };

  return (
    <div className="chatbot border border-gray-300 rounded p-6">
      <h2 className="text-2xl font-semibold mb-4">Chatbot Thông Minh</h2>
      <div className="chat-window h-64 overflow-y-auto border border-gray-200 rounded p-4 mb-4 bg-white">
        {messages.length === 0 && <p className="text-gray-500">Bắt đầu trò chuyện với chatbot...</p>}
        {messages.map((msg, idx) => (
          <p key={idx} className={msg.startsWith('User:') ? 'text-right text-blue-600' : 'text-left text-gray-800'}>
            {msg}
          </p>
        ))}
        {error && <p className="text-red-600">{error}</p>}
      </div>
      <div className="chat-input flex space-x-2">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Nhập tin nhắn..."
          className="flex-grow border border-gray-300 rounded px-3 py-2"
          onKeyDown={(e) => { if (e.key === 'Enter') sendMessage(); }}
        />
        <button
          onClick={sendMessage}
          className="bg-red-800 text-white px-4 py-2 rounded hover:bg-red-900 transition"
        >
          Gửi
        </button>
      </div>
    </div>
  );
}
